.actions {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  min-height: 59px;
  margin: 0 auto;
  text-align: center;
}
.actions button {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7px 13px;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: #875735;
  background-color: #ffe7be;
  border-color: #a66a3a;
  border-width: 5px;
  border-style: solid;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 3px 5px 2px 0px rgba(0, 0, 0, 0.33),
    0px 3px 1px -2px rgba(65, 65, 65, 0.12);
}
.actions button:disabled {
  color: #54453b;
  background-color: #a5a19f;
  border-color: #54453b;
}
.roll-action span.button-subtitle {
  text-transform: lowercase;
  font-size: 11px;
}

.roll-action,
.collect-action,
.next-round-action {
  flex: 1;
  padding: 0 5px;
}
.roll-action span {
  display: block;
  text-transform: lowercase;
  font-size: 14px;
}
.collect-action .score {
  font-size: 12px;
}
.skull-symbol {
  width: 32%;
  position: relative;
  top: -70%;
  left: 75%;
  display: block;
  transform: rotate(15deg);
  animation: stamp 0.4s cubic-bezier(0.4, 0, 1, 1) forwards;
}

@keyframes stamp {
  from {
    opacity: 0;
    transform: rotate(0deg) scale(3);
  }
  to {
    transform: rotate(15deg) scale(1);
  }
}
